export default {
  name: "TopDropdown",
  props: {
    //是否需要mask
    mask: {
      type: Boolean,
      default: true
    },
    //控制显示
    show: {
      type: Boolean,
      default: false
    },
    //背景颜色
    bgcolor: {
      type: String,
      default: "#f2f2f2"
    },
    //padding-bottom rpx
    paddingbtm: {
      type: Number,
      default: 0
    },
    //高度rpx
    height: {
      type: Number,
      default: 580
    },
    //移动距离 需要计算
    translatey: {
      type: Number,
      default: 0
    }
  },
  methods: {
    handleClose() {
      if (!this.show) {
        return;
      }
      this.$emit('close', {});
    },
    px(num) {
      return uni.upx2px(num) + "px"
    }
  }
}
